body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.color-container {
  max-width: 600px;
  max-height: 600px;
}

.color-wrapper {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.color-box {
  width: 100px;
  height: 100px;
  margin: 4px;
  border-radius: 10px;
  cursor: pointer;
}
.text {
  font-weight: 600;
  margin-left: 20px;
}

footer {
  margin: 20px;
}
